import React, {useState} from "react";
import axios from 'axios';
import "./ContactForm.css"

const ContactForm = () => {

    const [formData, setFormData] = useState({
        firstname: "",
        lastname: "",
        email: "",
        message: ""
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    const generateMessage = (formData) => (
    `  ${formData.firstname} ${formData.lastname}\n
        :
    ${getCurrentDateTimeIsrael()}\n
    Email   :
    ${formData.email}\n
    :
    \r${formData.message}`
    );

    const getCurrentDateTimeIsrael = () => {
        const currentDate = new Date();
        const options = {
            timeZone: 'Israel',
            hour12: false,
            hour: '2-digit',
            minute: '2-digit',
            day: '2-digit',
            month: '2-digit',
            year: 'numeric'
        };
        return currentDate.toLocaleString('en-IL', options);
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (formData.firstname.trim() === "" || formData.lastname.trim() === "" || formData.email.trim() === "" || formData.message.trim() === "") {
            alert("    ");
            return;
        }
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(formData.email)) {
            alert("     ");
            return;
        }
        setFormData(prevState => ({
            ...prevState,
            firstname: "",
            lastname: "",
            email: "",
            message: "",
        }));
        try {
            const message = generateMessage(formData);
            await sendMessageToTelegram(message);
            alert("  ");
        } catch (e) {
            alert(e.message)
        }
    };

    const sendMessageToTelegram = async (message) => {
        try {
            const API_TOKEN = process.env.REACT_APP_BOT_TOKEN;
            const CHAT_ID = process.env.REACT_APP_CHAT_ID;

            const url = `https://api.telegram.org/bot${API_TOKEN}/sendMessage`;
            const data = {
                chat_id: CHAT_ID,
                text: message,
            };
            await axios.post(url, data);
        } catch (e) {
            throw new Error('    ');
        }
    };


    return (
        <div className="contact-form-content">
            <form onSubmit={handleSubmit}>
                <div className="name-container">
                    <input
                        type="text"
                        name="firstname"
                        placeholder="  *"
                        value={formData.firstname}
                        onChange={handleChange}
                        required
                    />
                    <input
                        type="text"
                        name="lastname"
                        placeholder="  *"
                        value={formData.lastname}
                        onChange={handleChange}
                        required
                    />
                </div>
                <input
                    type="email"
                    name="email"
                    placeholder=" Email    *"
                    value={formData.email}
                    onChange={handleChange}
                    required
                />
                <textarea
                    name="message"
                    placeholder="     *"
                    rows={3}
                    value={formData.message}
                    onChange={handleChange}
                    required
                />
                <button type="submit"></button>
            </form>
        </div>
    )
};

export default ContactForm;